import React, { Component } from 'react'
import EditableList from '@/components/EditableList'
import UserSearchBox from '@/components/SearchBox/UserSearchBox'
import NerdIcon from '@/icons/nerd.svg'
import compareBy from '@/utilities/compareBy'
import './Participants.scss'

const addParticipants = (map, users, addition) => {
  users.forEach(user => {
    map.set(user._id, {
      ...(map.get(user._id) || user),
      ...user,
      ...addition,
    })
  })
}

export default class Participants extends Component {
  static defaultProps = {
    className: '',
    editors: [],
    owners: [],
    players: [],
  }

  static getDerivedStateFromProps(props, state) {
    const { editors, owners, players } = props
    const { edits } = state

    const map = new Map()
    addParticipants(map, players, { level: 'player', order: 3 })
    addParticipants(map, editors, { level: 'editor', order: 2 })
    addParticipants(map, owners, { level: 'owner', order: 1 })
    addParticipants(map, edits, { edited: true })

    const participants = Array.from(map.values()).sort(compareBy('order'))
    return { ...state, participants }
  }

  state = {
    edits: [],
    participants: [],
  }

  handleAddUser = user => {
    const participants = [...this.state.participants]
    if (!participants.find(p => p._id === user._id)) {
      participants.push({ ...user, edited: true, level: 'player', order: 3 })
      participants.sort(compareBy('order'))

      this.setState({ participants })
    }
  }

  render = () => {
    const { className } = this.props
    const { participants } = this.state

    return (
      <div className={`participants ${className}`}>
        <ul className="participant-list">
          {participants.map(user => (
            <li className="participant row" key={user._id}>
              <NerdIcon className="nerd icon" />
              <div className="display">
                <span className="name">{user.name}</span>
                <span className="username">{user.username}</span>
              </div>
              <EditableList
                className="role"
                options={['player', 'editor', 'owner']}
                defaultValue={user.level}
              />
            </li>
          ))}
        </ul>
        <UserSearchBox
          className="add-user"
          onSelect={this.handleAddUser}
        />
      </div>
    )
  }
}
